let _this=null;

 const Hoc=(HocComponent)=>{
  return class HocClass extends React.Component{
   
  
     constructor(){
         super();
         _this=this;
         this.state={ toggleStatus:false }

     }

     toggle() {
              console.log('toggle==',_this)
              _this.setState({
          toggleStatus: !_this.state.toggleStatus
        })
      }

      render() {
        return (
          <HocComponent
            {...this.props}
            toggle={this.toggle}
            toggleStatus={this.state.toggleStatus}
          />
        )
  }
}
 }


export default Hoc