
import React from 'react';
import {
  SafeAreaView,
  StyleSheet,
  TouchableOpacity,
  View,
  Text,
} from 'react-native';


class Child2 extends React.Component{

    constructor (props){
        super();
        this.state={v2:props.value ? props.value:-1 }

    }
    
    static getDerivedStateFromProps(nextProps,prevState){
         console.log('nextProps==',nextProps)
         console.log('prevState==                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                   ',prevState)
         if(nextProps.value!=prevState.v2){
           return {v2:nextProps.value}
         }
      return null;
    }

    shouldComponentUpdate(nextProps, nextState){
      console.log('SCU_nextProps==',nextProps)
      console.log('SCU_nextState==',nextState)
      return true;
    }


    componentDidUpdate(prevProps,prevState){
      console.log('componentDidUpdate_prevProps==',prevProps)
      console.log('componentDidUpdate_prevState==',prevState)
    

    }

    render(){
        console.log('child2 render')
          return (

            <View>
            { 
                <TouchableOpacity  onPress={()=>{ this.props.updateValue()  }}>
                   <View>
                   <Text>{''+this.state.v2}</Text>
                   </View>
                    </TouchableOpacity>
                 
            }
                </View>
          )

    }

}

export default Child2;