import Chidl1 from './src/Screens/Child1';
import Chidl2 from './src/Screens/Child2';



let _this=null;
class App extends React.Component{

  constructor (){
    super();
    _this=this;
    this.spinValue = new Animated.Value(0)
    this.state={ value:1  }
  }

  updateValue(){
    _this.setState({value:_this.state.value+1})
  }


  componentDidMount(){
     this.spin();
  //  this.interval=setInterval(()=>{
  //   this.setState({value:0})
  //  },2000)
  }
  componentWillUnmount(){
    // clearInterval(this.interval)
  }

  spin(){
    this.spinValue.setValue(0.5)
    Animated.timing(
      this.spinValue,
      {
        toValue: 1,
        duration: 2000,
        easing: Easing.linear
      }
    ).start(()=>this.spin())
  }

  render(){
    console.log('parent render')
    const spin = this.spinValue.interpolate({
      inputRange: [0, 1],
      outputRange: ['0deg', '360deg']
    })
    return(
     <SafeAreaView>
       <View style={{margin:20}}>
        <TouchableOpacity   onPress={()=>{this.updateValue()}} >
        <Text>parent</Text>
          </TouchableOpacity>

          <Animated.Image
        style={{
          width: 227,
          height: 200,
          opacity:this.spinValue,
         
         }}
          source={{uri:'https://www.davidegazze.eu/wp-content/uploads/2019/08/test.png'}}
      />
         <Chidl1  updateValue={this.updateValue} value={this.state.value}/>
         <Chidl2  updateValue={this.updateValue} value={this.state.value} name={this.state.value}  />

         </View>
       </SafeAreaView>
    )
  }
}


export default App;
